import { Grid, Hidden } from "@material-ui/core";
import {
  FeedTemplateProps,
  useConfig,
  useInfiniteFeed,
} from "gatsby-theme-advanced";
import React from "react";
import { Helmet } from "react-helmet";
import CategoryList from "../../../components/Categories/CategoryList";
import CategorySelect from "../../../components/Categories/CategorySelect";
import FeedListing from "../../../components/FeedListing";
import SiteTitle from "../../../components/SiteTitle";
import Layout from "../../../layouts";
import "./styles.scss";

const Feed = ({ pageContext }: FeedTemplateProps): JSX.Element => {
  const { feedListing, feedElementRef } = useInfiniteFeed(pageContext);

  const config = useConfig();

  // feed type   title . react-helmet       .
  // feeId    .
  // Override the title for non-index feeds
  const getTitleOverride = () => {
    if (pageContext.feedId) {
      if (pageContext.feedType === "tag")
        return (
          <Helmet
            title={`Posts tagged as "${pageContext.feedId}" | ${config.website.title}`}
          />
        );

      if (pageContext.feedType === "category")
        return (
          <Helmet
            title={`Posts in category "${pageContext.feedId}" | ${config.website.title}`}
          />
        );
    }

    return null;
  };

  return (
    <Layout>
      <Grid container className="feed-wrapper" ref={feedElementRef}>
        {getTitleOverride()}
        <Grid item xs={12}>
          <SiteTitle />
        </Grid>
        <Hidden mdUp>
          <Grid item xs={12}>
            <CategorySelect selectedCategory={pageContext.feedId ?? "All"} />
          </Grid>
        </Hidden>
        <Grid item xs={12} md={9} lg={8}>
          <FeedListing listing={feedListing} />
        </Grid>
        <Hidden smDown>
          <Grid item xs={12} md={3} lg={4} className="category-list-container">
            <CategoryList selectedCategory={pageContext.feedId ?? "All"} />
          </Grid>
        </Hidden>
      </Grid>
    </Layout>
  );
};

export default Feed;
