import {
  TableOfContentItem,
  TableOfContentsType,
} from "gatsby-theme-advanced/src/types";
import React, { useEffect, useState } from "react";
import "./styles.scss";

const MaximumDepth = 2;

type Props = {
  tableOfContents: TableOfContentsType;
  className: string;
};

// #  ID  .
const getIds = (items: TableOfContentItem[], depth: number = 1) => {
  depth++;
  return items.reduce<string[]>((acc, item) => {
    if (item.url) {
      // url has a # as first character, remove it to get the raw CSS-id
      acc.push(item.url.slice(1));
    }
    if (item.items && depth <= MaximumDepth) {
      acc.push(...getIds(item.items, depth));
    }
    return acc;
  }, []);
};

const useActiveId = (itemIds: string[]) => {
  const [activeId, setActiveId] = useState(``);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveId(entry.target.id);
          }
        });
      },
      { rootMargin: `0% 0% -80% 0%` }
    );

    itemIds.forEach((id) => {
      const headingElement = document.getElementById(id);
      if (headingElement) {
        observer.observe(headingElement);
      }
    });

    return () => {
      itemIds.forEach((id) => {
        const headingElement = document.getElementById(id);
        if (headingElement) {
          observer.unobserve(headingElement);
        }
      });
    };
  }, [itemIds]);

  return activeId;
};

const renderItems = (
  items: TableOfContentItem[],
  activeId: string,
  depth: number = 1
) => {
  depth++;
  return (
    <ul>
      {items.map((item) => (
        <li key={item.url}>
          <a
            href={item.url}
            className={activeId === item.url?.slice(1) ? "active" : ""}
          >
            {item.title}
          </a>
          {depth <= MaximumDepth &&
            item.items &&
            renderItems(item.items, activeId, depth)}
        </li>
      ))}
    </ul>
  );
};

const TableOfContents = ({ tableOfContents, ...rest }: Props) => {
  if (!tableOfContents?.items) {
    return <></>;
  }

  const idList = getIds(tableOfContents.items);
  const activeId = useActiveId(idList);

  return <div {...rest}>{renderItems(tableOfContents.items, activeId)}</div>;
};

export default TableOfContents;
