import { graphql, useStaticQuery } from "gatsby";
import { CategoryGroupingQuery, GroupedCategory } from "./types";

export const useCategoryList = (): GroupedCategory => {
  const resp = useStaticQuery<CategoryGroupingQuery>(graphql`
    query CategoryList {
      groupedCategory: allMdx(limit: 2000) {
        totalCount
        group(field: frontmatter___category) {
          name: fieldValue
          count: totalCount
        }
      }
    }
  `);
  return [
    {
      name: "All",
      count: resp.groupedCategory.totalCount,
    },
    ...resp.groupedCategory.group,
  ];
};
