import { Box, Grid, Hidden, Typography } from "@material-ui/core";
import { PostTemplateProps, SEO, Types } from "gatsby-theme-advanced";
import React from "react";
import Article from "../../../components/Article";
import TableOfContents from "../../../components/Article/TableOfContents";
import FeedListing from "../../../components/FeedListing";
import Layout from "../../../layouts";
import "./styles.scss";

const Post = ({ data, pageContext }: PostTemplateProps): JSX.Element => {
  const post = Types.queryIntoPost(data);

  return (
    <Layout>
      <>
        <SEO post={post} />
        <Grid container className="post-container">
          <Grid item xs={12} md={12} lg={8} className="post">
            <Article post={post} />
            <Box className="related-posts-wrapper">
              <Typography variant="h2">Related posts:</Typography>
              <FeedListing
                listing={pageContext.relatedPosts.map(Types.jsonPostIntoPost)}
              />
            </Box>
          </Grid>
          <Hidden mdDown>
            <Grid item xs={12} md={12} lg={4}>
              {post.tableOfContents && (
                <TableOfContents
                  tableOfContents={post.tableOfContents}
                  className="table-of-contents"
                />
              )}
            </Grid>
          </Hidden>
        </Grid>
      </>
    </Layout>
  );
};

export default Post;
