import { Chip, List, ListItem, ListItemText } from "@material-ui/core";
import React from "react";
import { useCategoryList } from "../query";
import "./styles.scss";

type Props = {
  selectedCategory: string;
};

const CategoryList = ({ selectedCategory }: Props): JSX.Element => {
  const categoryList = useCategoryList();
  return (
    <>
      <List component="ul" className="category-list">
        {categoryList.map((category) => {
          const { name, count } = category;
          const url = category.name === "All" ? "/" : `/category/${name}`;

          return (
            <ListItem
              key={name}
              button
              component="a"
              selected={name === selectedCategory}
              href={url}
              className="item"
            >
              <Chip label={count} className="count" />
              <ListItemText primary={name} />
            </ListItem>
          );
        })}
      </List>
    </>
  );
};

export default CategoryList;
