import { Box, Card, CardActionArea, Typography } from "@material-ui/core";
import { format } from "date-fns";
import { navigate } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { Types } from "gatsby-theme-advanced";
import React from "react";
import "./styles.scss";

type Props = {
  post: Types.Post;
};

const ItemNarrow = ({ post }: Props): JSX.Element => {
  const image = post.coverImg ? getImage(post.coverImg) : undefined;

  return (
    <Card
      variant="outlined"
      onClick={() => setTimeout(() => navigate(post.slug), 300)}
      className="item-narrow"
    >
      <CardActionArea className="card-area">
        <Box className="layout">
          <Box className="thumbnail-container">
            {image && (
              <GatsbyImage
                image={image}
                alt={post.coverImageAlt}
                className="thumbnail"
              />
            )}
          </Box>
          <Box className="text-container">
            <Typography variant="h3" className="title">
              {post.title}
            </Typography>
            <Typography className="date" variant="subtitle2">
              {` Time to read: ${post.timeToRead} minutes`}
              {"  "} {format(post.datePublished, "LLLL d, y")}
            </Typography>
            <span
              className="description"
              dangerouslySetInnerHTML={{
                __html: post.excerpt ?? "",
              }}
              itemProp="description"
            />
          </Box>
        </Box>
      </CardActionArea>
    </Card>
  );
};

export default ItemNarrow;
