import { Container, CssBaseline, Grid } from "@material-ui/core";
import { SEO, useConfig } from "gatsby-theme-advanced";
import * as React from "react";
import { useState } from "react";
import { AppContext } from "../components/AppContext";
import CustomAppBar from "../components/CustomAppBar";
import Footer from "../components/Footer";
import "./styles.scss";
import CustomDrawer from "../components/CustomDrawer";

type MainLayoutProps = {
  children: React.ReactElement;
};

const MainLayout = ({ children }: MainLayoutProps): JSX.Element => {
  const config = useConfig();
  const [drawerOpen, setDrawerOpen] = useState(false);
  return (
    <React.Fragment>
      <CssBaseline />
      <SEO />
      <AppContext.Provider value={{ drawerOpen, setDrawerOpen }}>
        <CustomAppBar siteTitle={config.website.title} />
        <Grid container className="content-wrapper">
          <Grid item xs={12} style={{ flexGrow: 1 }}>
            <Container maxWidth="lg">{children}</Container>
          </Grid>
          <Grid item xs={12} className="footer-contxainer">
            <Footer />
          </Grid>
        </Grid>
        <CustomDrawer />
      </AppContext.Provider>
    </React.Fragment>
  );
};

export default MainLayout;
