import { Box, Typography } from "@material-ui/core";
import Chip from "@material-ui/core/Chip";
import { format } from "date-fns";
import { navigate } from "gatsby";
import { Types } from "gatsby-theme-advanced";
import React from "react";
import "./styles.scss";

type Post = Types.Post;

type IntroProps = {
  post: Post;
};

const renderTags = (post: Post): JSX.Element | null => {
  if (!post.tags || post.tags.length === 0) return null;

  const tagList = post.tags.map((tag) => (
    <Chip
      size="small"
      onClick={() => navigate(`/tag/${tag}`)}
      label={tag}
      component="a"
      href="#chip"
      clickable
    />
  ));

  return (
    <span className="tag-container">
      <Typography className="title" variant="subtitle2" component="span">
        {" "}Tags:
      </Typography>
      {"  "}
      {tagList}
    </span>
  );
};

const renderCategory = (post: Post): JSX.Element | null => {
  const { category } = post;
  if (!category) return null;

  return (
    <span className="category-container">
      <Typography className="title" variant="subtitle2" component="span">
        {" "}Category:
      </Typography>
      {"  "}
      <Chip
        size="small"
        onClick={() => navigate(`/category/${category}`)}
        label={category}
        component="a"
        href="#chip"
        clickable
      />
    </span>
  );
};

const Intro = ({ post }: IntroProps): JSX.Element => {
  return (
    <div className="article-intro">
      <Typography variant="h1" className="title">
        {post.title}
      </Typography>
      <Box className="meta-data">
        <Typography className="date" component="span" variant="subtitle2">
          {" "} {`Time to read: ${post.timeToRead} minutes`}
        </Typography>
        <Typography className="date" component="span" variant="subtitle2">
          {" "} {format(post.datePublished, "LLLL d, y")}
        </Typography>
        {renderCategory(post)}
        {renderTags(post)}
      </Box>
    </div>
  );
};

export default Intro;
